import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RegisterSocietyRoutingModule } from './register-society-routing.module';
import { PageHeaderModule } from '../../shared';
import { RegisterSocietyComponent } from './register-society.component';
import {  MatInputModule, MatButtonModule, MatSelectModule, MatCardModule } from '@angular/material';
import { FormsModule, ReactiveFormsModule,  } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material';
import {MatStepperModule} from '@angular/material/stepper';

@NgModule({
  imports: [CommonModule,
    FormsModule,
    ReactiveFormsModule,
     RegisterSocietyRoutingModule, PageHeaderModule,
    MatStepperModule,
    MatFormFieldModule,
    MatCardModule,
    MatInputModule,
    MatSelectModule,
    MatButtonModule,
  ],
  declarations: [RegisterSocietyComponent]
})
export class RegisterSocietyModule { }



